* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

header {
  justify-content: center;
  align-items: center;
  height: 10vh;
  background: #ffc4b4;
  text-align: center;
  color: #fff;
  letter-spacing: 2px;
  font-weight: 400;
  padding: 2vh;
}

section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
  background: #ffdbb4;
}

form {
  position: relative;
  max-width: 80vw;
  padding: 2vh 5vh;
  background: #ffdbb4;
  border-radius: 15px;
  text-align: center;
  box-shadow: -5px -5px 10px rgba(0, 0, 0, 0.08),
    5px 5px 15px rgba(255, 255, 255, 0.5);
}

h1,
h2,
h3,
input {
  word-wrap: break-word;
  align-items: center;
  text-align: center;
  color: #ff3600;
  background: #ffdbb4;
  font-weight: 400;
  text-transform: lowercase;
  letter-spacing: 2px;
  padding: 2vh 0vh;
  border: none;
  outline: none;
  border-radius: 5vh;
}

input {
  width: 60vw;
  max-height: 20vh;
  box-shadow: -5px -5px 10px rgba(0, 0, 0, 0.08),
    5px 5px 15px rgba(255, 255, 255, 0.5);
}

.submit {
  width: 50px;
  height: 50px;
  margin: 5vh;
  border-radius: 50%;
}

.hidden {
  display: none;
}
